      description: '<p>Waterloo Silver Tongues! We\'re a Toastmasters Club meeting every Monday evening from 7:00-8:30pm at a unique location within Element Hair Salon at The Boardwalk in Waterloo Ontario.</p> <p>Silver Tongues are a a fresh, fun and no judgement zone to flex your speaker muscles. Our club is dedicated in helping each other build confidence to achieve greatness in all aspects of communication, including written and impromptu speeches, body language and listening.</p> <p>Experienced Toastmasters present the fundamentals of public speaking in a relaxed, enjoyable atmosphere. Members learn through an education program called Pathways, A learning experience tailored to your personal and professional goals and gives the member the opportunity to develop many skills relevant to an evolving global marketplace.</p> <p>The end result....you will become a more confident and poised speaker.</p> <p>If Your Dreams Don\'t Scare You They\'re Not Big Enough!</p> <p>Now more than ever is the time to find our voice</p> ',
