      description: '<p>The Bitcoin Bay is Toronto’s longest running and most popular blockchain and cryptocurrency meetup. Our goal is to foster education, innovation, and networking for like-minded people in the technology &amp; greater regional communities. We hold meetups in downtown Toronto, Mississauga, and Kitchener</p> <p>Community meetups are a big part of blockchain and cryptocurrency technology. We interact with others to ask questions and talk about different ideas and projects to build a more robust ecosystem.</p> <p>In the future we hope to build an inclusive, blockchain-agnostic, and flourishing community in Waterloo Region. Beyond our regional, provincial, and national borders - The Bitcoin Bay has been able to establish a flourishing WhatApp community from all over the globe. Be sure to ask about these groups to be added to them at our meetups</p> ',
