      description: '<p>DevHouse Waterloo is an event giving programmers and designers the opportunity to meet other creative people and learn from each other - whatever the topic may be.</p> <p>You can bring an idea, or a project you\'ve been working on, and present it to the group for feedback or help. Bits will be flowing (wifi is provided), projector will be available, food will be served, and space is provided by Shopify.</p> ',
